@import 'color';

// TodoTemplate.scss
.TodoTemplate {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  background-color: var(--main);
  display: flex;
  display: -webkit-flex;
  justify-content: center;

  &-Contents {
    margin: auto;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    -ms-flex-direction: column;
    align-items: center;
  }
}

.TodoTitle {
  display: flex;
  display: -webkit-flex;
  align-items: center;
  font-size: 1.8rem;
  color: var(--white);
  margin-bottom: 10px;

  &-Icon {
    margin-right: 8px;
  }
}

.TodoList {
  width: 500px;
  height: 500px;
  max-height: 500px;
  position: relative;
  border: 2px solid var(--white);
  border-radius: 10px;
  margin-bottom: 10px;
  padding: 1.5rem;
  overflow-x: hidden;
  overflow-y: auto;

  &-NoList {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.TodoInput {
  width: 100%;
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid var(--white);
  padding: 0 4px;

  &-Input {
    flex: 1;
    outline: none;
    border: none;
    color: var(--white);
    background-color: var(--main);
    padding: 5px 2px;
    font-size: 1.2rem;

    &::placeholder {
      color: var(--white);
    }
  }

  &-Button {
    color: var(--white);
    font-size: 1.4rem;
    cursor: pointer;
  }
}

.TodoItem {
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding-bottom: 6px;
  border-bottom: 1px solid var(--white);
  font-size: 1.2rem;

  -moz-user-select: none;
  -webkit-user-drag: none;

  &-Completed {
    text-decoration-line: line-through;
    text-decoration-color: var(--red);
  }

  & > * {
    max-width: 100%;
    max-height: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
  }

  &-Icons {
    display: flex;
    display: -webkit-flex;
    align-items: center;

    &-Pen {
      color: var(--green);
    }

    &-Close {
      color: var(--red);
      font-size: 1.9rem;
    }
  }
}

.TodoModal {
  width: 40%;
  height: 40%;
  background-color: var(--white);
  padding: 1.6rem;
  border-radius: 10px;
  overflow-x: hidden;
  overflow-y: auto;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -ms-flex-direction: column;
  align-items: center;

  &-Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 2;
  }

  &-Title {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    margin-bottom: 12px;

    & > div {
      font-size: 1.5rem;
      margin-right: 6px;
    }
  }

  &-Contents {
    display: flex;
    display: -webkit-flex;
    flex-grow: 1;
    flex-direction: column;
    -ms-flex-direction: column;
    justify-content: center;
    align-items: center;

    &-Input {
      outline: none;
      border: none;
      border-bottom: 2px solid var(--main);
      margin-bottom: 10px;
      padding: 5px 0;
      font-size: 1.3rem;
    }

    &-Button {
      width: 100%;
      height: 50px;
      outline: none;
      border: none;
      color: var(--white);
      background-color: var(--main);
      font-size: 1.2rem;
      cursor: pointer;
    }
  }
}
